class Hubcounter extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            data: [],
            mounted: false
        }

        this.update()
    }

    update() {
        // var link = 'https://api.github.com/repos/HackerTon/end2endselfdriving/stats/contributors'
        var link = 'https://api.github.com/users/HackerTon/repos'


        fetch(link).then(res => res.json())
            .then(json => {
                this.setState({ data: json, mounted: !this.mounted })
            })
            .catch(err => console.error(this.props.url))
    }

    render() {
        if (this.state.mounted) {
            return (
                <div className='container'>
                    {this.state.data.map(data => {
                        return (
                            <div className='row'>
                                <div className='col'>
                                    {data.name}
                                </div>
                                <div className='col'>
                                    {data.owner.login}
                                </div>
                                <div className='col'>
                                    {data.description}
                                </div>
                                <div className='col'>
                                    <a href={data.html_url}>{data.html_url}</a>
                                </div>
                            </div>
                        )
                    })}
                </div>
            )
        } else {
            return (
                <div className='row'>
                </div>
            )
        }

    }

}

class Button extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            passTrue: true,
        };

        this.onclick = this.onclick.bind(this)

    }

    onclick() {
        this.setState(state => ({
            passTrue: !state.passTrue
        }));
    }

    render() {
        console.log('debug')

        return (
            <div>
                <button onClick={this.onclick}>{this.state.passTrue ? 'You Found mE' : 'Oi!'}</button>

                {this.state.passTrue ? '' : 'Press! Press! Press! You kid ahhh?!'}
            </div>
        )
    }
}

